import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';
import Filter from './Filter';
import YuJingChuLi from "./YuJingChuLi";
import JianKangJiBie from "./JianKangJiBie";
import XueYuanFenXi from "./XueYuanFenXi";
import RenQunFenXi from "./RenQunFenXi";
import XueShengFenBu from "./XueShengFenBu";
import QiShiFenXi from "./QuShiFenXi";


export default class FenXi extends Component {

    constructor(props) {
        super(props);
        this.state= {
            filterValue: null,
            filterName: null,
            tabs:[],
        };
        // 获取筛选条件中的value用请求， 获取筛选条件的name用于标注生效条件
        this.getFilterValueAndName = this.getFilterValueAndName.bind(this);
    }

    // 获取筛选条件中的value用请求， 获取筛选条件的name用于标注生效条件
    getFilterValueAndName(a, b) {
        this.setState({
            filterValue: a,
            filterName: b
        })
    }

    render () {
        // console.log(this.state.filterName,"filterName")
        // console.log(this.state.filterValue,"filterValue")
        return (
            <Fragment>
                <Row style={{margin:"-20px"}}>
                    <Col span={24} style={{lineHeight:'60px', backgroundColor:"rgba(255, 255, 255, 0.60)"}}>
                        <Filter getFilterValueAndName = {this.getFilterValueAndName}/>
                    </Col>
                </Row>
                {
                    this.state.filterValue == null ?
                        null
                        :
                        <Row>
                            <Col span={12} style={{marginTop:20,paddingRight:10}}>
                                <YuJingChuLi filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>

                            <Col span={12} style={{marginTop:20,paddingLeft:10}}>
                                <JianKangJiBie filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>

                            <Col span={24} style={{marginTop:20}}>
                                <XueYuanFenXi filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>

                            <Col span={12} style={{marginTop:20,paddingRight:10}}>
                                <RenQunFenXi filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>

                            <Col span={12} style={{marginTop:20,paddingLeft:10}}>
                                <XueShengFenBu filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>

                            <Col span={24} style={{marginTop:20}}>
                                <QiShiFenXi filterName = {this.state.filterName} filterValue = { this.state.filterValue} />
                            </Col>
                        </Row>
                }

            </Fragment>
        )
    }
}


