import React, {Component, Fragment} from 'react';
import {Select, Button} from 'antd';
import {postAction} from "../../../axios";
import {connect} from 'react-redux';


const { Option, OptGroup } = Select;

// const curYear = new Date().getFullYear();
// const curMonth = new Date().getMonth()+1;
// let yearList = [];
// for (let i = 0; i < 6; i++) {
//     yearList.push({
//         name: curYear - i + "-" + ((curYear - i) + 1),
//         value: curYear - i
//     })
// }

class Filter extends Component {

    constructor(props) {
        super(props);
        this.state = {
            // 学院
            curCollege:'',
            curCollegeName: "全部学院",
            collegeLists:[],
            // 专业
            curMajor:'',
            curMajorName:'全部专业',
            majorLists:[
            	{
            		majorCode: "",
            		majorName: "全部专业"
            	}
            ],
            
            curGrade:'',
            curGradeName: "全部年级",
            gradeLists:[
            	{
            		value: "全部年级",
            		name: ""
            	}
            ],
            //时间
            curNear:2018,  //最近
            curNearName:'',
            nearLists:[
                {name:'最近一月', value:1},
                {name:'最近一周', value:2}
            ],
            curYear: 2018,
            curYearName: "2018-2019",
            yearList:[
				{name: "2018-2019", value: 2018 },
				{name: "2017-2018", value: 2017 },
				{name: "2016-2017", value: 2016 },
			],

        };
        this.changeFilter = this.changeFilter.bind(this); // 改变筛选项
        this.getColledgeLists = this.getColledgeLists.bind(this); // 获取学院list
        this.getMajorLists = this.getMajorLists.bind(this); // 获取专业list
        this.getGradeLists = this.getGradeLists.bind(this); // 获取年级list
        this.sureBtn = this.sureBtn.bind(this); // 筛选条件确定按钮
    }
    
    // 筛选条件确定按钮
    sureBtn () {
    	let a={
    		curYear : this.state.curYear,
            // curNear: this.state.curNear,
    		curCollege: this.state.curCollege,
    		curMajor:this.state.curMajor,
    		curGrade:this.state.curGrade,
    	};
    	
    	let b={
    		curYearName : this.state.curYearName,
            // curNearName: this.state.curNear === 1 ? this.state.nearLists[0].name : this.state.nearLists[1].name,
    		curCollegeName: this.state.curCollegeName,
    		curMajorName:this.state.curMajorName,
    		curGradeName:this.state.curGradeName,
    	};
    	this.props.getFilterValueAndName(a, b);
    }
    
    // 获取年级list
    getGradeLists() {
    	postAction(
    		"/bigdata/school/getGradeList",
    		{
    			year: this.state.curYear,
    			collegeCode: this.state.curCollege,
    			majorCode: this.state.curMajor
    		}
    	).then(
    		(res) =>{
    			if (res.success){
    				this.setState({
    					curGrade: "",
    					gradeLists: res.obj,
						curGradeName: "全部年级",
    				})
    			}
    		}
    	)
    }
    
    // 获取专业list
    getMajorLists () {
    	postAction(
    		"/bigdata/school/listMajorByCollege",
    		{
    			collegeCode: this.state.curCollege
    		}
    	).then(
    		(res) => {
    			if (res.success) {
    				this.setState({
    					curMajor: "",
    					majorLists: res.obj,
						curMajorName: "全部专业",
    				})
    			}
    		}
    	)
    }
    
     // 获取学院list
    getColledgeLists(){
    	postAction(
    		"/bigdata/school/listCollege",
    	).then(
    		(res)=>{
    			if (res.success) {
    				this.setState({
    					curCollege: "",
    					collegeLists: res.obj,
						curCollegeName: "全部学院",
    				})
    			}
    		}
    	)
    }
    
    changeFilter(num, value, option){
    	if (num === 1){ // 变更学院
    		this.setState(
	    		{
	    			curCollege: value,
		            curCollegeName: option.props.children,
	    		},
	    		() =>{
	    			this.getMajorLists()
	    			this.getGradeLists()
	    		}
    		)
    	} else if (num === 2){ // 变更专业
    		this.setState(
    			{
	    			curMajor: value,
		            curMajorName: option.props.children,
	    		},
    			() => {
    				this.getGradeLists()
    			}
    		)
    	} else if (num === 3){ // 变更年级
    		this.setState(
    			{
    				curGrade: value,
	            	curGradeName: option.props.children,
    			}
    		)
    	} else if (num === 4) { //时间
            this.setState({
                curNear: value,
                curYear: value,
				curYearName: option.props.children,
				curNearName: option.props.children,
            })

        }
    }

    render () {

        return (
            <Fragment>
                <span style={{marginLeft:"25px"}}>学院：</span>

                <Select 
                	value={this.state.curCollege}
                	placeholder="请选择学院" 
                	style={{ width: 180, marginRight:"10px" }}
                	onChange={this.changeFilter.bind(this, 1)}
                >
                    {
                        this.state.collegeLists.map(
                        	(item, index) => <Option 
                        				key={index} 
                        				title={item.collegeName}
                        				value={item.collegeCode}
                        			  >
                        				{item.collegeName}
                        			  </Option>
                        )
                    }
                </Select>

                <span style={{lineHeight:"32px"}}>专业：</span>
                <Select 
                	value={this.state.curMajor}
                	placeholder="请选择专业" 
                	style={{ width: 150, marginRight:"10px" }}
                	onChange={this.changeFilter.bind(this, 2)}
                >
                    {
                        this.state.majorLists.map(
                        	(item, index) => <Option 
                        				key={index} 
                        				title={item.majorName}
                        				value={item.majorCode}
                        			  >
                        				{item.majorName}
                        			  </Option>)
                    }
                </Select>

                <span style={{lineHeight:"32px"}}>年级：</span>
                <Select 
                	value = {this.state.curGrade}
                	placeholder="请选择年级" 
                	style={{ width: 120, marginRight:"10px" }}
                	onChange={this.changeFilter.bind(this, 3)}
                >
                    {
                        this.state.gradeLists.map(
                        	(item, index) => <Option 
                        						key={index} 
                        						title={item.value}
                        						value={item.name}
                        					 >
                        						{item.value}
                        					 </Option>
                        )
                    }
                </Select>

                {/*时间*/}
				<span style={{lineHeight:"32px"}}>时间：</span>
				<Select
					placeholder="2018-2019"
					value={this.state.curNear}
					style={{ width: "160px", marginRight:10 }}
					onChange={this.changeFilter.bind(this, 4)}
				>
					<OptGroup label="最近">
						{
							this.state.nearLists.map((item, index) =>
								(
									<Option key={index} value={item.value}>{item.name}</Option>
								))
						}
					</OptGroup>
					<OptGroup label="学年">
						{
							this.state.yearList.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
						}
					</OptGroup>
				</Select>

                <Button type="primary" icon="search" onClick={this.sureBtn}>搜  索 </Button>
            </Fragment>
        )
    }
    
    componentDidMount() {
    	this.getColledgeLists()
    	this.getGradeLists()
    	this.sureBtn()
    }
    
}

const mapStateToProps = (state) => {
    return {
        xueQiStatus: state.xueqiReducer.xueQiStatus
    }
}


export default connect(mapStateToProps, null)(Filter)



