import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import {postAction} from "../../../../axios";
import ReactDOM from "react-dom";
// import {hostUrl} from "../../../../env";
import MainCard from '../../../../common/components/MainCard';
import BarChart from "./BarChart";


export default class XueYuanFenXi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            barData: [
                // { name: '九龙湖校区', '男生': 40, '女生': 20 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', campus: '九龙湖校区', number: '2901', rate: '31%' }
            ],
            pagination: {
                current:1,
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }


    getData () {

        const {curCollege,curGrade, curYear} = this.props.filterValue;
        postAction(
            "/bigdata/mentality/getPercentByCollegeGender",
            {
                collegeCode: curCollege,
                grade: curGrade,
                time: curYear,
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0){
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _chartData.push({
                                name: item.collegeName,
                                "男生": Number(item.boyAmount),
                                "女生": Number(item.girlAmount)
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.collegeName,
                                boy: item.boyAmount,
                                girl: item.girlAmount
                            })
                        }
                    )
                    this.setState({
                        barData: _chartData,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        barData: [],
                        tableData: [],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    }


    //
    // //导出明细
    // getDetail () {
    //     const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
    //     window.location.href = `${hostUrl}/bigdata/student/exportStudentCampusDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    // }
    //
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'xueyuan-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
            },{
                title: '男生(人数)',
                dataIndex: 'boy',
            },{
                title: '女生(人数)',
                dataIndex: 'girl',
            }
        ];

        return (
            <MainCard
                title={<div>
                    <div className="common-title">心理健康学院分析
                    <div style={{fontSize:12,color:"rgba(0, 0, 0, 0.45)",fontWeight:400}}>  {this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curYearName === "最近一月" ? this.props.filterName.curYearName : this.props.filterName.curYearName === "最近一周" ? this.props.filterName.curYearName : this.props.filterName.curYearName + "学年" }
                    </div>
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:"心理健康学院分析"
                }}
                // detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "xueyuan-excel",
                    filename: "心理健康学院分析execl下载"
                }}
            >
                {this.state.tab === '1' ? (
                    <BarChart
                        height={this.props.height - 100}
                        data={this.state.barData}
                        padding={[10,40,60,60]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }}
                        colors={['#1890ff','#2FC25B']}
                    />
                ) : (

                    <React.Fragment>
                        <Table
                            bodyStyle={{height:300}}
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            onChange={this.handleTableChange}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


