import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import {postAction} from "../../../../axios";
import ReactDOM from "react-dom";
// import {hostUrl} from "../../../../env";
import MainCard from '../../../../common/components/MainCard';
import PieChart from './PieChart';


export default class YuJingChuLi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { name: '九龙湖校区', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', campus: '九龙湖校区', number: '2901', rate: '31%' }
            ],
            pagination: {
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {

        const {curCollege,curMajor,curGrade, curYear} = this.props.filterValue;
        postAction(
            "/bigdata/mentality/getPercentByDealStatus",
            {
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                time: curYear,
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0){
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _chartData.push({
                                name: item.finallyDealStatus,
                                count: item.amount
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                campus: item.finallyDealStatus,
                                number: item.amount
                            })
                        }
                    )
                    this.setState({
                        pieData: _chartData,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        pieData: [],
                        tableData: [],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }
    //
    // //导出明细
    // getDetail () {
    //     const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
    //     window.location.href = `${hostUrl}/bigdata/student/exportStudentCampusDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    // }
    //
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'campus-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '预警处理状态',
                dataIndex: 'campus',
            },{
                title: '预警处理状态数量',
                dataIndex: 'number',
            }
        ];

        return (
            <MainCard
                title={<div>
                    <div className="common-title">心理健康预警处理分析
                    <div style={{fontSize:12,color:"rgba(0, 0, 0, 0.45)",fontWeight:400}}>  {this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curYearName === "最近一月" ? this.props.filterName.curYearName : this.props.filterName.curYearName === "最近一周" ? this.props.filterName.curYearName : this.props.filterName.curYearName + "学年" }
                    </div>
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:"心理健康预警处理分析"
                }}
                // detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "campus-excel",
                    filename: "心理健康预警处理分析execl下载"
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        height={this.props.height - 100}
                        data={this.state.pieData}
                        padding={[0,10,60,10]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }} />
                ) : (

                    <React.Fragment>
                        <Table
                            bodyStyle={{height:300}}
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


